.App{
  text-align: center;
}
.App-logo {
  height: 180px;
}
#chart {
    background-color: #F5F2EB;
  border: 1px solid #CCC;
}

.bar {
  shape-rendering: crispEdges;
}

.bar-label{
    fill: #000;
  text-anchor: middle;
  font-size: 18px;
}

.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

.gridline path,
.gridline line{
  fill: none;
  stroke: #ccc;
  shape-rendering: crispEdges;
}
#submitbtnsummary{
  margin-left: 10px;
}
#multiline-flexible{
  text-align: center;
}
#FirstName{
  width: 200px;
}
#LastName{
  width: 200px;
}


